.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #dde4f5;
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.table-font{
  font-size: 2rem;
}

.navi-background-color{
  background-color: #192A53;
  color: rgb(255, 255, 255);
}

.navi-font-color{
  color: #192A53;
}

.navi-font-color{
  color: #192A53;
}

.navi-background-color :hover{
  background-color: #192A53;
  color: rgb(255, 255, 255);
}

.red-color{
  background-color: #F8263A;
}

.table-row-color{
  background-color: #f2f3f6;
}

.check-in-input {
  border: none;
  color: #192A53;
  background: transparent;
  border-bottom: 2px solid #fff;
  outline: none;
}

.nav-border-bottom {
  border-bottom: 5px solid #F8263A;
  outline: none;
}

.header-border-bottom {
  border-bottom: 20px solid #192A53;
  outline: none;
}

.title-border-bottom {
  border-bottom: 2px solid #192A53;
  outline: none;
}

.fade-in {
	opacity: 1;
	animation-name: fadeInOpacity;
	animation-iteration-count: 1;
	animation-timing-function: ease-in;
	animation-duration: 0.5s;
}

@keyframes fadeInOpacity {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

.pointer { 
  cursor: pointer; 
}

.invisible{
  visibility: hidden;
}

.btn:hover {
  color: #ffffff;
}

.btn:active {
  outline:none;
}

.selection-card{
  background-color: #e0e3eb;
  color: #192A53;
  border: 3px solid #192A53;
}

.selection-button{
  border: 3px solid #192A53;
  color: #192A53;
  outline: none;
}

.finish-button{
  background-color: #192A53;
  color: #ffffff;
}

.selection-button:hover{
  background-color: #192A53;
  color: #ffffff;
}

.selection-card:hover {
  background-color: #192A53;
  color: #ffffff;
}
